@import "../mixins.scss";
@import "./auth-form.scss";

* {
    margin : 0;
    padding : 0;
    box-sizing : border-box;
    font-family: 'PT Sans',sans-serif;
}


#content {
    min-height : 100vh;
    background-color : #252525;
    display : flex;
    justify-content: space-between;
    align-items: center;


    @include respond("mobile") {
        .block-wrapper {
            .block {
                display: none;
            }
        }
    }

    .block-wrapper {
        display: flex;
        position : relative;
        height : 100vh;
        flex : 0 0 20%;

        .block {
            width: 35px;
            height : 100%;
            background-color : $base-color-pink;
            margin-right: .75rem;
            border-bottom-right-radius: .5rem;
        }

        .block-1 {
            height : 35%;
        }

        .block-2 {
            height: 25%;
        }

        .block-3 {
            height: 15%;
        }
    
    }

    .block-wrapper-right {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;

        .block {
            margin : 0;
            border-radius: 0;
            margin-left : .75rem;
            border-top-left-radius: .5rem;
        }

        .block-1 {
            height : 15%;
        }

        .block-2 {
            height: 25%;
        }

        .block-3 {
            height: 35%;
        }

    }

    .help-link {
        margin-top : 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        a {
            color : white;
            font-size : 1rem;
            text-decoration: none;
            margin-left : .5rem;
        }

        a:hover {
            font-weight : 600;
            color : $base-color-pink;
        }
    }

   
}